export default {
	data () { //引入PopupPicker组件 引入提现路由
		return {
			buttons: [/*{
        type: 'primary',
        text: 'Primary button',
        onClick: this.changeIcon.bind(this)
      },*/ {
        type: 'default',
        text: '返回',
        link: '/Center/income',
				sss: '',
				kkk: '',
      }]
		}
	},
	components:{
		
	},
	methods: {
		esc: function () {
			this.$router.back()
		},
		formatBankNumber (o) {
			if(o == null || o == undefined){
				return '暂无数据'
			}else{
				return o.substr(0,4) + '***' + o.substr(-4)
			}
		}
	},
	created(){
		this.sss = this.$route.query.sss;
		this.kkk = this.$route.query.kkk;
	}
}



